
import Vue from "vue"

export default Vue.extend({
  props: {
    type: {
      type: String,
      required: false,
      default: "information",
    },
    title: {
      type: String,
      required: false,
      default: "",
    },
  },
  computed: {
    getIcon() {
      if (this.type === "warning") return "❗️"
      else if (this.type === "danger") return "🚨"
      else if (this.type === "success") return "✅"
      else return "💡"
    },
  },
})
